.checkbox .text-input {
  background-color: transparent;
}

.checkbox__input:checked + .checkbox-label::after {
  content: "";
  position: absolute;
  left: -47px;
  top: 2px;
  width: 22px;
  height: 16px;
  background-color: transparent;
  background-image: url("../img/icon-check.svg");
  background-repeat: no-repeat;
  background-position: 0 50%;
}

.checkbox__input:hover + .checkbox-label::before {
  background-color: #e5e5e5;
}

.checkbox__input:active + .checkbox-label::before {
  background-color: #d5d5d5;
}

.checkbox__input:checked:active + .checkbox-label::after {
  background-image: url("../img/icon-check-active.svg");
}

.checkbox-label {
  position: relative;
  cursor: pointer;
}

.checkbox-label::before {
  position: absolute;
  content: "";
  top: -13px;
  left: -60px;
  width: 46px;
  height: 46px;
  padding: 0;
  background-color: #f2f2f2;
  border-radius: 4px;
  cursor: pointer;
}

